@font-face {
    font-family: 'Raleway';
    src: url('Raleway.ttf');
} 
html, body {
  background-color: #2ab7ca;
  color: #000;
  font-size:100%;
  font:inherit;
  font-family: Raleway;
}

.flex-center {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.third-width {
  width: 33%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.video-mask {
  -webkit-mask-image: -webkit-radial-gradient(circle, white 100%, black 100%); 
  -webkit-transform: rotate(0.000001deg);
  border-radius: 25px;
  height: 449px;
  overflow: hidden; 
}

.video-mask video {
  transform: rotateY(180deg);
  -webkit-transform:rotateY(180deg); /* Safari and Chrome */
  -moz-transform:rotateY(180deg); /* Firefox */
  margin-top: -27px;
}

.time:hover {
  background-color: #e5ce89;
  cursor: pointer;
  color: #f4f4f8;
}

.time {
  margin-top: 40px;
  padding: 30px 55px 30px 55px;
  background-color: #fed766;
  border-radius: 20px;
  font-size: 85px;
  cursor: pointer;
  font-weight: bold;
}

.description{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column; 
  width: 200px;
  font-size: 170px;
  text-align: center;
  margin-left: -20%;
}

.reference_img {
  width: 140%;
  margin-left: -15%;
}

.result {
  margin-left: 15px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction:column;
      flex-direction:column;
  text-align: center;
}

.result img {
  margin-top: 15px;
  border-radius: 20px;
  width: 50%;
}

.app {
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -webkit-transition: background-color 0.4s;
  -o-transition: background-color 0.4s;
  transition: background-color 0.4s;
}

.content {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
}

.correct {
  color: #fed766;
  font-size: 60px;
  margin-top: 15px;
  font-weight: bold;
}

.incorrect {
  color: #fed766;
  font-size: 60px;
  margin-top: 15px;
  font-weight: bold;
}

.instructions {
  font-size: 50px;
  text-align: center;
  margin: 30px 8% 50px 8%;
  font-weight: bold;
}

.final-result {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  font-size: 55px;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  height: 100vh;
  -ms-flex-direction: column;
      flex-direction: column;
}

.final-result .score {
  font-size: 60px;
  margin-top: 50px;
  margin-bottom: 50px;
  color: #fed766;
}




